









































































































































import { Component, Mixins } from 'vue-property-decorator'
import PopupMixin from '@/mixins/popup'
import ReportRepository from '@/services/repositories/report'
import getRepository from '@/services'
import FilterMixin from '@/mixins/filterAuditContractMixin'
@Component
export default class individual extends Mixins(PopupMixin, FilterMixin) {
  isFullScreen = false
  class_Active!: any
  form: any = {}
  content = ''
  public isOpenShareStory = false
  getTopRanking: any = []
  salesFullNameTop1 = ''
  totalPercentF = 0
  salesMemberAvatar = ''
  api!: ReportRepository
  isLoading = false
  image = ''
  shareLoading = false
  facebook: undefined
  param = {
    month: 6,
    year: 2021,
    pageIndex: 1,
    pageSize: 10,
    orderByClause: 4
  }
  top: any = []
  async getHonors(): Promise<void> {
    this.isLoading = true
    await this.api.getHonors(this.param).then((res: any) => {
      // this.top = res.splice(0, 2)
      // this.getTopRanking = res.splice(3, 9)
      // this.salesFullNameTop1 = this.top?.[0]?.salesFullName
      // this.totalPercentF = this.top?.[0]?.resultMonthRevenueTotalPercentF
      // this.salesMemberAvatar = this.top?.[0]?.salesMemberAvatar
      this.getTopRanking = res
      this.salesFullNameTop1 = this.getTopRanking?.[0]?.salesFullName
      this.totalPercentF = this.getTopRanking?.[0]?.resultMonthRevenueTotalPercentF
      this.salesMemberAvatar = this.getTopRanking?.[0]?.salesMemberAvatar
      this.isLoading = false
    })
  }
  created(): void {
    this.form = this.user
    this.api = getRepository('report', {
      districtId: this.user?.districts?.[0].id
    })
    this.openRankingTable()
  }
  openRankingTable(): void {
    this.getHonors()
  }
  close(): void {
    this.setOpenPopup({
      popupName: 'popuptop',
      isOpen: false
    })
  }
  indexMethod(index: number): number {
    return index + 1
  }
}
